import { makeStyles } from "@material-ui/core";
import styles from "../styles/PseudoStyles";
const useStyles = makeStyles(styles);
const PseudoScience = (props) => {
  const { url, headings, title, para, pre } = props;
  const first = parseInt(pre[0]);
  const second = parseInt(pre[1]);
  console.log(first, second);
  const classes = useStyles();
  return (
    <div className={classes.pseudoContainer}>
      <div style={{ order: `${first}` }}>
        <img className={classes.pic} src={url} alt="image not displayed" />
      </div>
      <div
        className={classes.pseudoTextContainer}
        style={{ order: `${second}` }}
      >
        <h1 className={classes.factHeading}>{title}</h1>
        <div>
          <p
            style={{
              color: "rgb(20,43,111)",
              fontWeight: 400,
              textAlign: "left",
            }}
          >
            {para}
          </p>
        </div>
        <div className={classes.listContainer}>
          {headings.map((each) => {
            return (
              <div>
                <a className={classes.vitamins}>{each}</a>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};
export default PseudoScience;
