import { makeStyles } from "@material-ui/core";
import styles from "../styles/HomeImgStyles";
const useStyles = makeStyles(styles);
const HomeImg = () => {
  const classes = useStyles();
  return (
    <div
      style={{ zIndex: 1, position: "relative", height: "100%", width: "100%" }}
    >
      <div className={classes.background}>
        <h1 className={classes.heading}>
          <div style={{ position: "relative", marginBottom: 14 }}>
            The future
          </div>
          <div style={{ position: "relative", right: 90, marginBottom: 14 }}>
            of health
          </div>
          <div
            style={{
              fontWeight: 400,
              fontStyle: "italic",
              fontStretch: "expanded",
            }}
          >
            <em>— is clear.</em>
          </div>
        </h1>
        <div>
          <button className={classes.shopButton}>
            <span
              style={{
                fontFamily: "LL Circular",
                fontSize: "16px",
                lineHeight: "1.4rem",
                fontWeight: 600,
                color: "white",
                textAlign: "center",
              }}
            >
              Shop All
            </span>
          </button>
        </div>
      </div>
    </div>
  );
};
export default HomeImg;
