import ShoppingCards from "../components/ShoppingCards";
import { makeStyles } from "@material-ui/core";
import styles from "../styles/ShoppingCardStyles"

const useStyles=makeStyles(styles)
const AllShopCards=()=>{
    const classes=useStyles()
    return (
        <div className={classes.cardContainer}>
            <ShoppingCards url="https://res.cloudinary.com/bharsdmj/image/upload/v1629654424/Category-Multivitamin_mlxmpi.webp" heading="MultiVitamin"/>
            <ShoppingCards url="https://res.cloudinary.com/bharsdmj/image/upload/v1629657432/Category-Protein__1__c4mldb.webp" heading="Protein"/>
            <ShoppingCards url="https://res.cloudinary.com/bharsdmj/image/upload/v1629657432/Category-Pregnancy__1__o3e9yl.webp" heading="Pregnancy"/>
            <ShoppingCards url="https://res.cloudinary.com/bharsdmj/image/upload/v1629657432/Category-Bundles_bpvfsw.webp" heading="Bundles"/>
        </div>
    )
}
export default AllShopCards