import logo from "./logo.svg";
import "./App.css";
import NavComponent from "./components/NavComponent";
import HomeImg from "./components/HomeImg";
import ShoppingCards from "./components/ShoppingCards";
import AllShopCards from "./images/AllShopCards";
import GrpIcons from "./components/GrpIcons";
import PseudoScience from "./components/PseudoScience";
import Animation from "./components/Animation";
function App() {
  return (
    <div className="App">
      <NavComponent />
      <div style={{ padding: "24px 28px", height: "100%" }}>
        <HomeImg />
      </div>
      <div style={{ marginTop: 32, marginBottom: 70 }}>
        <AllShopCards />
      </div>
      <div style={{ marginBottom: 80 }}>
        <GrpIcons />
      </div>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-start",
          position: "relative",
        }}
      >
        <div style={{ marginBottom: 48 }}>
          <PseudoScience
            url="https://res.cloudinary.com/bharsdmj/image/upload/v1629659254/home-feat-dyson_jy4e88.webp"
            headings={[
              "MultiVitamin for Women 18+",
              "The Prenatal MultiVitamin",
              "Multivitamin for 18+",
              "Essential Protein  Daily  Shake 18+",
            ]}
            title="We're not about pseudoscience  and half truths"
            para="
        From Omega-3 DHA from microalgae to regeneratively-farmed pea protein, our scientists studied diets and genetics to make daily essentials based on what we need."
            pre={[1, 2]}
          />
        </div>
      </div>
      <div style={{ marginBottom: 48 }}>
        <PseudoScience
          url="https://res.cloudinary.com/bharsdmj/image/upload/v1629659853/Home-Map_zcwwal.webp"
          headings={[
            "Who we Are ",
            "Meet Our Ingredients",
            "Our Clinical Study",
          ]}
          title="You Deserve Traceability"
          para="We share our sources, studies, and suppliers — daily essentials backed by the first visible supply chain of its kind."
          pre={[2, 1]}
        />
      </div>
    </div>
  );
}

export default App;
