import React from "react";
import { Navbar, Container, Nav } from "react-bootstrap";
import { makeStyles } from "@material-ui/core";
import styles from "../styles/NavStyles";

import "bootstrap/dist/css/bootstrap.min.css";
const useStyles = makeStyles(styles);
window.onscroll = function () {
  changeContent();
};
const changeContent = () => {
  const elem = document.getElementById("Navbar");
  console.log("hi", document.documentElement.scrollTop);
  if (document.documentElement.scrollTop > 50) {
    console.log("yes");
    elem.style.borderBottomColor = "rgb(204,204,204)";
    elem.style.borderBottomWidth = "1px";
    elem.style.borderBottomStyle = "solid";
    elem.style.borderTopWidth = "0px";
    elem.style.borderTopStyle = "none";
  } else {
    elem.style.borderBottomWidth = "0px";
    elem.style.borderTopWidth = "4px";
    elem.style.borderTopStyle = "solid";
  }
};

const NavComponent = () => {
  const classes = useStyles();

  return (
    /*<nav>
      <ul>
        <li>Menu</li>
        <li>Ritual</li>
        <li>Who We Are</li>
      </ul>
    </nav>*/
    <Navbar
      fixed="top"
      id="Navbar"
      className={`${classes.navContainer} ${classes.borderStyle}`}
    >
      <div style={{ width: "100%", margin: "0px auto" }}>
        <Container
          style={{
            marginLeft: "auto",
            marginRight: "auto",
            height: "100%",
            padding: "0px 15px",
          }}
        >
          <ul className={classes.listContainer}>
            <li href="#home" className={classes.listItem}>
              <button className={classes.button}>
                <p
                  className={classes.heading}
                  style={{ position: "relative", top: 8 }}
                >
                  Menu
                </p>
              </button>
              <span></span>
            </li>
            <li href="#home" className={classes.listItem}>
              <div>
                <a
                  href="url"
                  style={{
                    color: "#142b6f",
                    textDecoration: "none",
                    backgroundColor: "transparent",
                    cursor: "pointer",
                  }}
                >
                  <p
                    style={{
                      fontSize: 24,
                      fontWeight: 700,
                      position: "relative",
                      left: 150,
                      top: 8,
                    }}
                    className={classes.heading}
                  >
                    Ritual
                  </p>
                </a>
              </div>
            </li>
            <li href="#features" className={classes.listItem}>
              <p
                style={{
                  fontSize: 16,
                  fontWeight: 600,
                  position: "relative",
                  left: 240,
                  top: 8,
                }}
                className={classes.heading}
              >
                Who We Are
              </p>
            </li>
            <li href="#features" className={classes.listItem}>
              <svg
                className="path"
                style={{ right: 60, position: "relative" }}
                width="24"
                height="24"
                version="1.1"
                viewBox="0 0 24 24"
              >
                <g
                  fill="none"
                  fill-rule="evenodd"
                  stroke="none"
                  stroke-width="1"
                >
                  <g>
                    <rect
                      width="24"
                      height="24"
                      x="0"
                      y="0"
                      fill="#FFF"
                      fill-opacity="0"
                    ></rect>
                    <path
                      stroke="#142B6F"
                      stroke-linejoin="round"
                      stroke-width="2"
                      d="M20.5,6.5 L20.5,23 L4,23 L4,6.5 L20.5,6.5 Z M12.25,1 C14.5281746,1 16.375,2.84682541 16.375,5.125 C16.375,5.43937389 16.3000569,5.88826911 16.1501708,6.47168566 C11.4355784,6.44324758 8.85377775,6.42767434 8.40476894,6.42496595 L8.33387282,6.42453831 C8.33387282,6.42453831 8.125,5.57904881 8.125,5.125 C8.125,2.84682541 9.97182541,1 12.25,1 Z"
                    ></path>
                  </g>
                </g>
              </svg>
            </li>
          </ul>
        </Container>
      </div>
    </Navbar>
  );
};
export default NavComponent;
